import React from 'react';

import BaseQRCode from 'qrcode.react';

const QRCode = ({size}) => <div className='container'>
    <BaseQRCode value={window.location.href} size={size}/>
    <style jsx>{`
        .container {
            display: none;
        }

        @media print {
            .container {
                display: block;

                position: absolute;
                top: 0;
                right: 0;

                z-index: 1;
            }
        }
    `}</style>
</div>;

export default QRCode;
