import React from 'react';

import {
    BrowserRouter as Router,
    Switch,
    Route,
    Redirect,
} from 'react-router-dom';

import MuiThemeProvider from '@material-ui/core/styles/MuiThemeProvider';
import theme from './theme';

import NavigationBar from './components/NavigationBar';

import Projects from './pages/Projects';
import About from './pages/About';


const routes = [
    {
        name: 'Projects',
        path: '/projects',
        component: <Projects/>
    },
    {
        name: 'About',
        path: '/about',
        component: <About/>
    },
];



class App extends React.PureComponent {
  /***** Setup *****/
  componentDidMount() {
    this.setScrollListener();
  }

  componentDidUpdate() {
    this.setScrollListener();
  }

  setScrollListener() {
    const root = document.getElementById('page-container');
    if (root === null) {
        setTimeout(this.setScrollListener.bind(this), 200);
        return;
    }

    this.handleScroll(root);
    root.addEventListener('scroll', () => this.handleScroll(root));
  }

  handleScroll(root) {
    // Prevent horizontal scroll
    if (root.scrollLeft > 0) {root.scrollLeft = 0;}

    // Record scroll data for css
    const position = root.scrollTop;
    document.documentElement.dataset.scroll = position;
  }


  /***** Rendering & Navigation *****/
  getTabs() {
      return Object.fromEntries(routes.map(
        ({name, path}) => [name, path]
      ));
  }

  renderRoutes() {
      return routes.map(
          ({path, component}) =>
          <Route key={path} path={path}>{component}</Route>
      );
  }

  render() {
    return <Router>
      <MuiThemeProvider theme={theme}>
      <NavigationBar tabs={this.getTabs()}/>
        <Switch>
          {this.renderRoutes()}
          <Route path='/'><Redirect to='/projects'/></Route>
        </Switch>
      </MuiThemeProvider>
    </Router>;
  }
}

export default App;
